@import '_variables';
@import '_global';

main {
	display: flex;
	align-items: center;
	width: 100%;
	
	.logo {
		text-align: center;
		
		h1 {
			margin: -50px 0 0 0;
			font-size: 25px;
		}
	}
	
	.column {
		text-align: center;
		
		a {
			color: $white;
			text-decoration: none;
			display: inline-block;
		}
	}
	
	.discord {
		a {
			background: url('../images/ico-discord.svg') no-repeat left center;
			padding: 20px 0 20px 65px;
		}
	}
	
	.mail {
		a {
			background: url('../images/ico-mail.svg') no-repeat left center;
			padding: 20px 0 20px 60px;
		}
	}
}

footer {
	width: 100%;
	text-align: center;
	
	color: rgba($white, 0.5);
	font-weight: 700;
}



@media screen and (max-width: $max-breakpoint-mobile) {
	main {
		flex-direction: column;
		justify-content: center;
		padding-top: 40px;
		
		.logo {
			order: 1;
			margin-bottom: 50px;
		}
		
		.discord {
			order: 2;
			margin-bottom: 10px;
			
			a {
				background-size: 15% auto;
				padding: 10px 0 10px 40px;
			}
		}
		
		.mail {
			order: 3;
			
			a {
				background-size: 15% auto;
				padding: 10px 0 10px 45px;
			}
		}
	}
	
	footer {
		margin-top: 100px;
	}
}

@media screen and (min-width: $min-breakpoint-mobile) {
	main {
		flex-direction: row;
		height: 90vh;
		
		.logo {
			flex: 0 0 40%;
		}
		
		.column {
			flex: 0 0 30%;
			
			a {
				span {
					position: relative;
					display: block;
					transition: all ease 300ms;
					
					&::after {
						content: '';
						position: absolute;
						left: 0;
						bottom: -6px;
						background: $vert;
						height: 1px;
						transition: all ease 300ms;
						width: 0;
					}
				}
				
				&:hover {
					span {
						color: $vert;
						
						&::after {
							width: 100%;
						}
					}
				}
			}
		}
		
		.discord {
			a {
				padding: 20px 0 20px 65px;
			}
		}
		
		.mail {
			a {
				padding: 20px 0 20px 60px;
			}
		}
	}
	
	footer {
		bottom: 20px;
		position: absolute;
		left: 0;
		right: 0;
	}
}