.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.hidden {
  display: none !important;
  visibility: hidden;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.relative {
  position: relative;
}

.txtright {
  text-align: right;
}

.txtleft {
  text-align: left;
}

.txtcenter {
  text-align: center;
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -ms-overflow-style: scrollbar;
  height: 100%;
  -webkit-overflow-scrolling: touch;
}

html *,
html *::before,
html *::after {
  box-sizing: border-box;
}

* {
  outline: none;
}

@-ms-viewport {
  width: device-width;
}

body {
  font-family: "Work Sans", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.3;
  color: #ffffff;
  background-color: #1E1E43;
  min-height: 100vh;
  position: relative;
}

main {
  display: flex;
  align-items: center;
  width: 100%;
}

main .logo {
  text-align: center;
}

main .logo h1 {
  margin: -50px 0 0 0;
  font-size: 25px;
}

main .column {
  text-align: center;
}

main .column a {
  color: #ffffff;
  text-decoration: none;
  display: inline-block;
}

main .discord a {
  background: url("../images/ico-discord.svg") no-repeat left center;
  padding: 20px 0 20px 65px;
}

main .mail a {
  background: url("../images/ico-mail.svg") no-repeat left center;
  padding: 20px 0 20px 60px;
}

footer {
  width: 100%;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 700;
}

@media screen and (max-width: 991px) {
  main {
    flex-direction: column;
    justify-content: center;
    padding-top: 40px;
  }
  main .logo {
    order: 1;
    margin-bottom: 50px;
  }
  main .discord {
    order: 2;
    margin-bottom: 10px;
  }
  main .discord a {
    background-size: 15% auto;
    padding: 10px 0 10px 40px;
  }
  main .mail {
    order: 3;
  }
  main .mail a {
    background-size: 15% auto;
    padding: 10px 0 10px 45px;
  }
  footer {
    margin-top: 100px;
  }
}

@media screen and (min-width: 992px) {
  main {
    flex-direction: row;
    height: 90vh;
  }
  main .logo {
    flex: 0 0 40%;
  }
  main .column {
    flex: 0 0 30%;
  }
  main .column a span {
    position: relative;
    display: block;
    transition: all ease 300ms;
  }
  main .column a span::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -6px;
    background: #3ACF6E;
    height: 1px;
    transition: all ease 300ms;
    width: 0;
  }
  main .column a:hover span {
    color: #3ACF6E;
  }
  main .column a:hover span::after {
    width: 100%;
  }
  main .discord a {
    padding: 20px 0 20px 65px;
  }
  main .mail a {
    padding: 20px 0 20px 60px;
  }
  footer {
    bottom: 20px;
    position: absolute;
    left: 0;
    right: 0;
  }
}
